import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController, ActionSheetController, Platform, ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
// import { HomePage } from "../home/home"

//API
import { Http } from "@angular/http"
import 'rxjs/add/operator/map'
import { UtilsProvider } from "../../providers/utils/utils"
import { Socket } from 'ng-socket-io';
// import { Observable } from 'rxjs/Observable';



@Component({
  selector: 'page-cart',
  templateUrl: 'cart.html',
})
export class CartPage {
	private showTvSeries:any = true
	private showMovies:any = true
	private friends:any;
	cartlist:any;
	moviecartlist:any;
	base_url:any;
	poster_sizes:any;
	tmdbConfigImages:any;
	backdrop_sizes:any;
	checkout:any;
	userinfo:any;
	user:any;
	connected:any;
	index:any;
	APIKEY = "35be3be17f956346becdba89d4f22ca1";
	users:any;
	

	constructor(
		public navCtrl: NavController, 
		public navParams: NavParams, 
		public platform: Platform,
		public viewCtrl:ViewController,
		public actionsheetCtrl: ActionSheetController,
		public alertCtrl: AlertController,
		public toastCtrl:ToastController,
		public utilsProvider: UtilsProvider,
		public storage: Storage,
		public socket: Socket, 
		public http:Http){
		
			this.socket.on("emitconnectedusers",(data)=>{
				this.connected = data
				// console.log(this.connected)
				this.ionViewDidLoad()
			})		
	}



	ionViewWillEnter(){
		// this.socket.emit("getconnectedusers")
		this.activeUsers()
	}


	ionViewDidLoad(){	
		//get friends
		this.storage.get('friend')
		.then((value)=>{
			this.friends = value
		})			


		//Load user info
        this.storage.get('user').then((value) => { 
            this.user = value.phone_number; 
        });   

		this.cartlist=this.utilsProvider.getCartTitle();
		this.moviecartlist=this.utilsProvider.getMovieCart()

		if(Array.isArray(this.cartlist)=== true){
			console.log(this.cartlist)
			if(this.cartlist.length <= 0){
				this.showTvSeries = false
				console.log("no series")
			}
	
			// if(this.moviecartlist.length <= 0){
			// 	this.showMovies = false
			// }
		}else if(Array.isArray(this.cartlist) === false){
			this.showTvSeries = false
		}

		if(Array.isArray(this.moviecartlist) === true){
			if(this.moviecartlist.length <= 0){
				this.showMovies = false
			}
		}else if(Array.isArray(this.moviecartlist) === false){
			this.showMovies = false;
		}

		

		//config
		this.http.get("https://api.themoviedb.org/3/configuration?api_key=35be3be17f956346becdba89d4f22ca1")
	  	.map(res => {
	  		this.base_url = res.json().images.base_url
	  		this.poster_sizes = res.json().images.poster_sizes[0]
	  		this.backdrop_sizes = res.json().images.poster_sizes[2]
	  	})
	  	.subscribe(tmdbConfig => {
		})
	}

	//reload connectedd users
	activeUsers(){
		return this.socket.emit("getconnectedusers")
	}



	//delete
	onDelete(get){
		let cart = this.utilsProvider.getCart()
		let index = cart.indexOf(get)
		this.utilsProvider.removeFromCart(index)
		this.utilsProvider.removeFromTitle(index)
		this.ionViewDidLoad()
	}

	onDeleteMovie(get){
		let cart = this.utilsProvider.getMovieCart()
		let index = cart.indexOf(get)
		this.utilsProvider.removeFromMovieCart(index)
		this.ionViewDidLoad()
	}

  	//close movieModal
	onClose(remove = false){
		this.activeUsers()
	    this.navCtrl.pop()
	}

	//open action sheet
	openMenu(){
		//this.activeUsers()
		this.socket.on("getAllSockets",(data)=>{
			console.log(JSON.parse(data))
		})
		this.socket.emit("_activeUsers")
		let actionSheet = this.actionsheetCtrl.create({
			title: "Cart Actions",
			cssClass: 'action-sheets-basic-page',
			buttons:[
				{
					text:"Place Order",
					icon: !this.platform.is("ios")?"cash":null,
					handler:()=>{	




						//get connected
						let _connected = this.connected
						// console.log(_connected)						
						// console.log("Calling Place Order...")	

						this.socket.emit("_activeUsers")

						let _alert = this.alertCtrl.create()
						_alert.setTitle("Choose Distributor")
						// _alert.addInput({
						// 	name:"distributor",
						// 	placeholder:"type distributor here"
						// })

						// for(var key in _connected){
						// 	_alert.addInput({
						// 		type:"radio",
						// 		label:key,
						// 		value:_connected[key],
						// 	})
						// 	console.log(_connected)
						// }

						for(var i = 0; i < this.friends.length; i++){
							_alert.addInput({
								type:"radio",
								label:this.friends[i].phone_number,
								value:this.friends[i].phone_number,
							})
						}						

						_alert.addButton({
							text:"Cancel",
							role:"cancel",
							handler:()=>{
								this.ionViewDidLoad()
							}
						})

						_alert.addButton({
							text:"Ok",
							handler:(data)=>{
								console.log(data)
								// console.log(_connected)
								var _movieTvShowCart = {
									tvcart:this.cartlist,
									moviecart:this.moviecartlist,
									phone_number:data,
									// socket:data.socket
								}

								this.utilsProvider.postMovieTvShowCart(_movieTvShowCart)
								.subscribe((result)=>{
									console.log(result)
								})

								// console.log("emitting data to server...")
								// this.socket.emit("add-cart",_movieTvShowCart);

								this.utilsProvider.emptyArray()

								let toast = this.toastCtrl.create({
									message:"Order Placed",
									duration:800,
									position:"middle"
								})
								toast.present();								
							}
						})

						_alert.setCssClass('alertCustomCss' )
						_alert.present()


					}
				},
				{
					text: 'Cancel',
			        role: 'cancel', 
			        icon: !this.platform.is('ios') ? 'close' : null,
			        handler:()=>{
			        	// console.log("Cancel Clicked")
			        }
		        }
			]
		})

		actionSheet.present()
	}

	cartEmpty(){
		let toast = this.toastCtrl.create({
			message:"Cart Empty",
			duration:800,
			position:"middle"			
		})

		toast.present()
	}
}




