import { Component } from '@angular/core';
import { NavController, LoadingController, ToastController } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { OauthProvider } from '../../providers/oauth/oauth'
import { HomePage } from '../home/home';
import { Facebook, FacebookLoginResponse } from "@ionic-native/facebook";
import { SocketProvider } from "../../providers/socket/socket";

import { Storage } from '@ionic/storage';

 
@Component({
  selector: 'login',
  templateUrl: 'login.html'
})
export class LoginPage {
 
    phone_number: string;
    password: string;
    loading: any;
    usersessionarray:any[]=[];
    connected:any;
    userData=null;
 
    constructor(
        private navCtrl: NavController, 
        private authService: AuthProvider, 
        private oauthProvider: OauthProvider,
        private facebook: Facebook,
        private loadingCtrl: LoadingController, 
        private socketProvider: SocketProvider,  
        private storage: Storage,
        private toastCtrll: ToastController){}
 
    ionViewDidLoad() {
 
        this.showLoader();
        this.getLoggedUser()
        
 
        //Check if already authenticated
        this.authService.checkAuthentication().then((res) => {
            // console.log("Already authorized");
            this.loading.dismiss();
            this.navCtrl.setRoot(HomePage);
        }, (err) => {
            // console.log("Not already authorized");
            this.loading.dismiss();
        });
 
    }
 
    login(){
 
        this.showLoader();
 
        let credentials = {
            phone_number: this.phone_number,
            password: this.password
        };
 
        this.authService.login(credentials).then((result) => {
            
            console.log(result)
            console.log(credentials)
            this.loading.dismiss();
            this.navCtrl.setRoot(HomePage);
        }, (err) => {
            this.loading.dismiss();
            // console.log(err);
            //LOGIN ERROR WRITE ALERT HERE!!!!
            let toast = this.toastCtrll.create({
                message:"Rectify login credentials and try again.",
                duration: 3000,
                position: "bottom",
                dismissOnPageChange: true 
            })

            toast.onDidDismiss(()=>{
                // console.log("Dismissed toast")
            });

            toast.present();
        }); 
    }

    register(){
        this.showLoader();

        let details = {
            phone_number: this.phone_number,
            password: this.password
        };

        // console.log(details)

        this.authService.createAccount(details).then((result) => {
            this.loading.dismiss();
            // console.log(result);
            this.navCtrl.setRoot(HomePage);
        }, (err) => {
            this.loading.dismiss();
        });

    }
 
    showLoader(){
 
        this.loading = this.loadingCtrl.create({
            content: 'Authenticating...'
        });
 
        this.loading.present();
 
    } 

    getLoggedUser(){
        this.storage.get("user")
        .then((loggeduser)=>{
            console.log(loggeduser)
            this.socketProvider.addUser({username:loggeduser.phone_number})
            // this.socket.emit("add_user",{username:loggeduser.phone_number})
        })
    }    
}


